import "./User.css";

const User = ({ Nombre, Correo, FechaNacimiento, Pais, index }) => {
  return (
    <div className="user-container w-50">
      <h1>Usuario Nro. {index}</h1>
      <p>Nombre: {Nombre}</p>
      <p>Correo electrnico: {Correo}</p>
      <p>Fecha de nacimiento: {FechaNacimiento}</p>
      <p>Pas de origen: {Pais}</p>
    </div>
  );
};

export default User;
